___CSS_LOADER_EXPORT___.push([module.id, "[data-v-3d9b42a4] .popper{background:#fff;border:2px;border-color:#030303;border-radius:20px;box-shadow:1;color:#030303;font-weight:700;padding:20px;text-transform:uppercase}[data-v-3d9b42a4] .popper #arrow:before{background:#fff}[data-v-3d9b42a4] .popper:hover,[data-v-3d9b42a4] .popper:hover>#arrow:before{background:#fff}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Rankings/Show.vue"],"names":[],"mappings":"AAmKA,0BACE,eAAmB,CAInB,UAAW,CACX,oBAA0B,CAF1B,kBAAmB,CADnB,YAAa,CAIb,aAAmB,CACnB,eAAiB,CANjB,YAAa,CAOb,wBACF,CAEA,wCACE,eACF,CAEA,8EAEE,eACF","sourcesContent":["<template>\n  <div>\n    <teleport to=\"head\">\n      <title>{{ title(\"Ranglijst\") }}</title>\n    </teleport>\n\n    <div class=\"flex items-center justify-between\">\n      <div\n        v-if=\"$page.props.club.details.lastProcessed == NULL\"\n        class=\"text-xl font-bold mx-2\"\n      >Ranglijst</div>\n      <div\n        v-else\n        class=\"text-xl font-bold mx-2\"\n      >Ranglijst na ronde {{$page.props.club.details.lastProcessed.round}}</div>\n\n    </div>\n    <div class=\"w-full\">\n      <div class=\"bg-white shadow-md rounded my-6\">\n\n        <table\n          v-if=\"Rankings.length > 0\"\n          class=\"sticky top-0 min-w-max w-full table-auto\"\n        >\n          <thead class=\"\">\n            <tr class=\"bg-gray-200 text-gray-600 uppercase text-sm leading-normal\">\n              <th class=\"py-3 px-6 text-center\">Speler</th>\n              <th class=\"py-3 px-6 text-center\">Score</th>\n              <th class=\"py-3 px-6 text-center hidden md:table-cell\">\n                Partijscore\n              </th>\n              <th class=\"py-3 px-6 text-center\">Waarde</th>\n\n            </tr>\n          </thead>\n          <tbody\n            v-for=\"(ranking, key) in Rankings.sort((a, b) => b.score - a.score)\"\n            class=\"text-gray-600 text-sm font-light\"\n            v-bind:key=\"ranking.id\"\n          >\n            <tr\n              class=\"border-b border-gray-200 hover:bg-gray-100\"\n              :class=\"{ 'bg-gray-50': checkOdd(key)}\"\n              @mouseenter=\"toggle(key)\"\n              @mouseleave=\"toggle(key)\"\n            >\n              <td class=\"text-center\">{{ ranking.user.name }}</td>\n              <td class=\"text-center\">\n                <Popper :arrow=\"true\">\n\n                  <div @click=\"fetchData(ranking.user.id)\">{{ ranking.score }}\n                    <Icon\n                      name=\"lookup\"\n                      class=\"inline-flex\"\n                    />\n                  </div>\n                  <template #content>\n                    <div v-if=\"data.length > 0\">\n                      Opbouw van de score:\n                      <hr>\n                      <table>\n                        <th>Partij (incl. bye en afwezigheid)</th>\n                        <th>Score</th>\n                        <tr\n                          v-for=\"(game, key) in data\"\n                          :key=\"game.id\"\n                        >\n                          <td>{{key + 1}}</td>\n                          <td>{{game.score}}</td>\n                        </tr>\n                      </table>\n                      <div>Huidige waarde: {{ranking.value}}</div>\n                    </div>\n                  </template>\n\n                </Popper>\n              </td>\n              <td class=\"text-center hidden md:table-cell\">\n                {{ ranking.gamescore }}\n              </td>\n              <td class=\"text-center\">\n                {{ ranking.value }}\n              </td>\n\n            </tr>\n            <tr\n              v-if=\"opened.includes(key)\"\n              class=\"bg-orange-100\"\n            >\n              <td class=\"text-center table-cell md:hidden\"><b>Partijscore</b><br>{{ ranking.gamescore }}</td>\n              <td class=\"text-center hidden md:table-cell\">\n                <b>Vorige waarde</b><br>{{ ranking.LastValue }}\n              </td>\n              <td class=\"text-center hidden md:table-cell\">\n                <b>Kleurbalans</b><br>\n                <div v-if=\"ranking.color == NULL\">-</div>\n                <div v-else>{{ ranking.color }}</div>\n\n              </td>\n              <td class=\"text-center\">\n                <b>Gespeelde Partijen</b><br>{{ ranking.amount }}\n              </td>\n              <td class=\"text-center\">\n                <b>TPR</b><br> {{ ranking.TPR }}\n              </td>\n            </tr>\n          </tbody>\n        </table>\n        <div v-else>\n          Er is nog geen ranglijst aangemaakt\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport Layout from \"@/Shared/Layout\";\nimport Popper from \"vue3-popper\";\nimport axios from \"axios\";\nimport Icon from \"@/Shared/Icon\";\n\nexport default {\n  layout: Layout,\n  name: \"Index\",\n  components: {\n    Popper,\n    Icon,\n  },\n  props: {\n    Rankings: Array,\n  },\n  data() {\n    return {\n      opened: [],\n      data: Array,\n    };\n  },\n  methods: {\n    checkOdd(key) {\n      if (key % 2 === 0 || key === 0) {\n        return true;\n      }\n      return false;\n    },\n    toggle(id) {\n      const index = this.opened.indexOf(id);\n      if (index > -1) {\n        this.opened.splice(index, 1);\n      } else {\n        this.opened.push(id);\n      }\n    },\n    async fetchData(userID) {\n      axios.get(\"/gamescore/\" + userID).then((response) => {\n        this.data = response.data;\n      });\n    },\n  },\n};\n</script>\n\n<style scoped>\n:deep(.popper) {\n  background: #ffffff;\n  padding: 20px;\n  box-shadow: 1;\n  border-radius: 20px;\n  border: 2px;\n  border-color: rgb(3, 3, 3);\n  color: rgb(3, 3, 3);\n  font-weight: bold;\n  text-transform: uppercase;\n}\n\n:deep(.popper #arrow::before) {\n  background: #ffffff;\n}\n\n:deep(.popper:hover),\n:deep(.popper:hover > #arrow::before) {\n  background: #ffffff;\n}\n</style>\n"],"sourceRoot":""}]);
